.wrapper {
    width: 100%;
    height: 100vh;
}
.float-btn {
    right: 24px;
    position: fixed;
    bottom: 0px;
    z-index: 7;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 8px;
    transform: none;
    transition: all 400ms cubic-bezier(0.65, 0, 0.35, 1) 0s;
}
.getapp-btn {
    width: 90px;
    height: 32px;
    font-size: 14px;
    color: rgba(22, 24, 35, 0.75);
}
.skeleton {
    width: 800px;
    margin-top: 10px;
}
