//Menu wrapper

.menu-list {
    width: 224px;
    background-color: #fff;
    box-shadow: rgb(0 0 0/12%) 0px 2px 12px;
    border-radius: 8px;

    .menu-item {
        margin-left: 0px;
    }
}

.menu-popper {
    display: flex;
    flex-direction: column;
    padding-bottom: 8px;
    max-height: min((100vh - 96px) - 60px, 734px);
}

.menu-body {
    overflow-y: auto;
}

//Menu items
.menu-item {
    width: 100%;
    justify-content: flex-start;
    border-radius: 0;
    padding: 11px 16px;
    line-height: 1.8rem;

    &.separate {
        border-top: 1px solid rgba(22, 24, 35, 0.12);
    }

    &:hover {
        background-color: rgba(22, 24, 25, 0.03);
    }
}

//Header

.header {
    position: relative;
    height: 50px;
    flex-shrink: 0;
}
.back-btn {
    width: 50px;
    height: 100%;
    background-color: transparent;
    cursor: pointer;
}
.header-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
